.img-button{
    border:solid 1px rgba(224, 224, 224, 1);;
    cursor: pointer;
}

.img-button48{
    width: 48px;
    height: 48px;
    border-radius: 10px;
}

.img-button32{
    width: 32px;
    height: 32px;
    border-radius: 5px;
}


.img-button24{
    width: 24px;
    height: 24px;
    border-radius: 5px;
}

.img-button56{
    width: 56px;
    height: 56px;
    border-radius: 5px;
}

.bckgrSize56{
    background-size: 56px;
}


.bckgrSize48{
    background-size: 48px;
}
.bckgrSize32{
    background-size: 32px;
    background-repeat: no-repeat;
} 
.bckgrSize24{
    background-size: 24px;
    background-repeat: no-repeat;
} 



.img-button:active{
    opacity: 0.5;
}


.img-button_disabled{
    opacity: 0.1;
}


.img-apply{
    background-image: url("data:image/svg+xml;base64,");
}

